:root, [data-bs-theme="light"] {
  --border-radius-medium: 12px;
  --border-radius-small: 8px;
  --box-shadow-large: 0 4px 15px var(--light-shadow);
  --box-shadow-medium: 0 4px 10px var(--light-shadow);
  --button-text-color2: white;
  --button-text-color: white;
  --font-weight-bold: 700;
  --font-weight-medium: 500;
  --gap-large: 25px;
  --gap-medium: 20px;
  --header-footer-padding-y: 15px;
  --header-footer-shadow-blur: 10px;
  --header-footer-shadow-color: var(--light-shadow);
  --header-footer-shadow-offset: 2px;
  --light-accent: #00b894;
  --light-bg: #f5f5f5;
  --light-border: #ddd;
  --light-card-bg: #fff;
  --light-input-bg: #fdfdfd;
  --light-input-border: #ccc;
  --light-input-focus: #8e7dff;
  --light-primary: #6c5ce7;
  --light-secondary: #a29bfe;
  --light-shadow: #0000001a;
  --light-text: #333;
  --line-height-default: 1.6;
  --link-underline-height: 2px;
  --margin-bottom-default: 20px;
  --max-width-md: 900px;
  --max-width-sm: 800px;
  --mobile-nav-height-offset: 70px;
  --padding-default: 20px;
  --section-padding-y: 80px;
  --transform-translate-y-lg: -8px;
  --transform-translate-y-md: -5px;
  --transform-translate-y-sm: -3px;
  --transition-ease: ease-in-out;
  --transition-speed: .3s;
  --button-hover-scale: 1.05;
  --button-hover-shadow: 0 8px 20px #0003;
  --card-hover-shadow: 0 10px 25px #00000026;
  --valid-color: #28a745;
  --invalid-color: #dc3545;
  --valid-shadow: #28a7454c;
  --invalid-shadow: #dc35454c;
  --about-p-font-size: 1.1em;
  --about-padding: 40px;
  --banner-h1-font-size: 4em;
  --banner-height: 600px;
  --banner-overlay-color: #0006;
  --banner-text-shadow: 0 2px 4px #0000007f;
  --card-h3-font-size: 1.8em;
  --card-img-height: 200px;
  --contact-icon-font-size: 20px;
  --contact-input-padding-x: 15px;
  --contact-input-padding-y: 8px;
  --dark-mode-toggle-font-size: 24px;
  --fixed-button-bottom: 30px;
  --fixed-button-font-size: 1.1em;
  --fixed-button-hover-color: #0ca;
  --fixed-button-hover-shadow: 0 8px 20px #00b89499;
  --fixed-button-padding-x: 25px;
  --fixed-button-padding-y: 15px;
  --fixed-button-right: 30px;
  --fixed-button-shadow: 0 5px 15px #00b89466;
  --icon-margin-right: 10px;
  --list-item-line-height: 1.5;
  --list-item-margin-bottom: 10px;
  --list-item-padding-left: 20px;
  --logo-font-size: 24px;
  --message-box-border-radius: 10px;
  --message-box-button-margin-top: 20px;
  --message-box-button-padding-x: 20px;
  --message-box-button-padding-y: 10px;
  --message-box-padding: 30px;
  --message-box-shadow: 0 8px 25px var(--light-shadow);
  --mobile-nav-link-font-size: 22px;
  --mobile-nav-link-padding-y: 15px;
  --mobile-nav-top-offset: 70px;
  --mobile-toggle-font-size: 30px;
  --search-input-width-initial: 150px;
  --search-input-width-focus: 300px;
  --social-icons-bottom: 30px;
  --social-icons-font-size: 30px;
  --terms-privacy-h1-margin-bottom: 40px;
  --terms-privacy-h2-margin-bottom: 15px;
  --terms-privacy-h2-margin-top: 30px;
  --terms-privacy-padding: 40px 20px;
  --search-input-placeholder-color: #666;
  --search-input-focus-bg: #fff;
  --search-input-focus-text: #333;
}

.dark-mode, [data-bs-theme="dark"] {
  --light-bg: #1a1a1a;
  --light-text: #e0e0e0;
  --light-primary: #7f71e5;
  --light-secondary: #bcb4ff;
  --light-accent: #00cda4;
  --light-card-bg: #2a2a2a;
  --light-border: #444;
  --light-shadow: #ffffff14;
  --light-input-bg: #3a3a3a;
  --light-input-border: #555;
  --light-input-focus: #a89bff;
  --button-hover-shadow: 0 8px 20px #ffffff1a;
  --card-hover-shadow: 0 10px 25px #ffffff14;
  --search-input-placeholder-color: #999;
  --search-input-focus-bg: #2a2a2a;
  --search-input-focus-text: #e0e0e0;
}

.navbar-brand {
  color: var(--light-primary);
  font-weight: var(--font-weight-bold);
}

.navbar-brand:hover {
  color: var(--light-secondary);
}

body {
  background-color: var(--light-bg);
  color: var(--light-text);
  transition: background-color var(--transition-speed) var(--transition-ease), color var(--transition-speed) var(--transition-ease);
  font-family: Inter, sans-serif;
  line-height: var(--line-height-default);
  margin: 0;
  padding: 0;
}

h1, h2, h3 {
  color: var(--light-primary);
  margin-bottom: var(--margin-bottom-default);
}

p {
  margin-bottom: 15px;
}

section {
  padding: var(--section-padding-y) 0;
  text-align: center;
}

header {
  background-color: var(--light-card-bg);
  color: var(--light-text);
  padding: var(--header-footer-padding-y) 0;
  box-shadow: 0 var(--header-footer-shadow-offset) var(--header-footer-shadow-blur) var(--header-footer-shadow-color);
  z-index: 1000;
  transition: background-color var(--transition-speed) var(--transition-ease), box-shadow var(--transition-speed) var(--transition-ease);
  position: sticky;
  top: 0;
}

footer {
  background-color: var(--light-card-bg);
  color: var(--light-text);
  text-align: center;
  box-shadow: 0 calc(-1 * var(--header-footer-shadow-offset)) var(--header-footer-shadow-blur) var(--header-footer-shadow-color);
  transition: background-color var(--transition-speed) var(--transition-ease), box-shadow var(--transition-speed) var(--transition-ease);
  margin-top: 50px;
  padding: 30px 0;
}

.footer-links {
  justify-content: center;
  gap: var(--gap-large);
  margin-bottom: var(--margin-bottom-default);
  flex-wrap: wrap;
  display: flex;
}

.footer-links a {
  color: var(--light-text);
  font-weight: var(--font-weight-medium);
  transition: color var(--transition-speed) var(--transition-ease);
  padding: 5px 0;
  text-decoration: none;
  position: relative;
}

.footer-links a:after {
  content: "";
  width: 0;
  height: var(--link-underline-height);
  background-color: var(--light-accent);
  transition: width var(--transition-speed) var(--transition-ease);
  position: absolute;
  bottom: 0;
  left: 0;
}

.footer-links a:hover:after {
  width: 100%;
}

.copyright {
  color: var(--light-text);
  font-size: .9em;
}

.btn {
  background-color: var(--light-primary);
  color: var(--button-text-color);
  border-radius: var(--border-radius-small);
  cursor: pointer;
  font-weight: var(--font-weight-medium);
  transition: transform var(--transition-speed) var(--transition-ease), box-shadow var(--transition-speed) var(--transition-ease), background-color var(--transition-speed) var(--transition-ease);
  box-shadow: var(--box-shadow-medium);
  border: none;
  padding: 12px 25px;
  text-decoration: none;
  display: inline-block;
}

.btn:hover {
  transform: translateY(var(--transform-translate-y-sm)) scale(var(--button-hover-scale));
  box-shadow: var(--button-hover-shadow);
  background-color: var(--light-secondary);
}

.logo {
  font-size: var(--logo-font-size);
  font-weight: var(--font-weight-bold);
  color: var(--light-primary);
  text-decoration: none;
}

.logo i {
  color: var(--light-accent);
}

.menu-toggle {
  font-size: var(--mobile-toggle-font-size);
  cursor: pointer;
  color: var(--light-text);
  display: none;
}

.nav-mobile {
  top: var(--mobile-nav-top-offset);
  width: 100%;
  height: calc(100vh - var(--mobile-nav-height-offset));
  background-color: var(--light-card-bg);
  box-shadow: 0 5px 15px var(--light-shadow);
  z-index: 999;
  transition: transform var(--transition-speed) ease-in-out;
  flex-direction: column;
  align-items: center;
  padding-top: 30px;
  display: none;
  position: fixed;
  left: 0;
  transform: translateX(100%);
}

.nav-mobile.active {
  display: block;
  transform: translateX(0);
}

.nav-mobile a {
  color: var(--light-text);
  font-size: var(--mobile-nav-link-font-size);
  padding: var(--mobile-nav-link-padding-y) 0;
  text-align: center;
  border-bottom: 1px solid var(--light-border);
  width: 100%;
  transition: background-color var(--transition-speed) var(--transition-ease), color var(--transition-speed) var(--transition-ease);
  text-decoration: none;
  display: block;
}

.nav-mobile a:last-child {
  border-bottom: none;
}

.nav-mobile a:hover {
  background-color: var(--light-bg);
  color: var(--light-primary);
}

#search-input {
  width: var(--search-input-width-initial);
  transition: width .3s ease-in-out;
}

#search-input:focus {
  width: var(--search-input-width-focus);
  background-color: var(--search-input-focus-bg);
  border-color: var(--light-input-focus);
  color: var(--search-input-focus-text);
}

#search-input::placeholder {
  color: var(--search-input-placeholder-color);
}

.dark-mode-toggle {
  font-size: var(--dark-mode-toggle-font-size);
  cursor: pointer;
  color: var(--light-text);
  transition: color var(--transition-speed) var(--transition-ease), transform var(--transition-speed) var(--transition-ease);
}

.dark-mode-toggle:hover {
  color: var(--light-primary);
  transform: rotate(15deg);
}

.card-grid {
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 30px;
  padding-top: 50px;
  display: grid;
}

.card {
  background-color: var(--light-card-bg);
  border-radius: var(--border-radius-medium);
  box-shadow: var(--box-shadow-large);
  padding: var(--gap-large);
  text-align: center;
  transition: transform var(--transition-speed) var(--transition-ease), box-shadow var(--transition-speed) var(--transition-ease);
  border: 1px solid var(--light-border);
  flex-direction: column;
  align-items: center;
  display: flex;
}

.card:hover {
  transform: translateY(var(--transform-translate-y-lg));
  box-shadow: var(--card-hover-shadow);
}

.card img {
  max-width: 100%;
  height: var(--card-img-height);
  object-fit: cover;
  border-radius: var(--border-radius-small);
  margin-bottom: var(--margin-bottom-default);
}

.card h3 {
  font-size: var(--card-h3-font-size);
  color: var(--light-primary);
  margin-bottom: 10px;
}

.card p {
  color: var(--light-text);
  margin-bottom: var(--margin-bottom-default);
  flex-grow: 1;
  font-size: 1em;
}

.card .btn {
  margin-top: auto;
}

.contact-content {
  flex-wrap: wrap;
  justify-content: center;
  gap: 40px;
  padding-top: 50px;
  display: flex;
}

.contact-methods, .contact-form {
  background-color: var(--light-card-bg);
  border-radius: var(--border-radius-medium);
  min-width: 300px;
  box-shadow: var(--box-shadow-large);
  text-align: left;
  border: 1px solid var(--light-border);
  transition: background-color var(--transition-speed) var(--transition-ease), box-shadow var(--transition-speed) var(--transition-ease);
  flex: 45%;
  padding: 30px;
}

.contact-methods h3, .contact-form h3 {
  color: var(--light-primary);
  margin-top: 0;
  margin-bottom: var(--gap-large);
  text-align: center;
}

.contact-methods ul {
  padding: 0;
  list-style: none;
}

.contact-methods ul li {
  align-items: center;
  margin-bottom: 15px;
  font-size: 1.1em;
  display: flex;
}

.contact-methods ul li i {
  color: var(--light-accent);
  font-size: var(--contact-icon-font-size);
  margin-right: 15px;
}

.contact-form form {
  gap: var(--gap-medium);
  flex-direction: column;
  display: flex;
}

.input-group {
  border: 1px solid var(--light-input-border);
  border-radius: var(--border-radius-small);
  padding: var(--contact-input-padding-y) var(--contact-input-padding-x);
  background-color: var(--light-input-bg);
  transition: border-color var(--transition-speed) var(--transition-ease), box-shadow var(--transition-speed) var(--transition-ease);
  align-items: center;
  display: flex;
  position: relative;
}

.input-group i {
  margin-right: var(--icon-margin-right);
  color: var(--light-text);
  transition: color var(--transition-speed) var(--transition-ease);
}

.input-group input, .input-group textarea {
  color: var(--light-text);
  background: none;
  border: none;
  outline: none;
  flex-grow: 1;
  padding: 5px 0;
  font-size: 1em;
}

.input-group textarea {
  resize: vertical;
  min-height: 100px;
}

.input-group input:focus, .input-group textarea:focus {
  box-shadow: none;
  border-color: #0000;
}

.contact-form .btn {
  align-self: center;
  width: fit-content;
  padding: 12px 40px;
}

.get-in-touch-fixed {
  bottom: var(--fixed-button-bottom);
  right: var(--fixed-button-right);
  z-index: 900;
  padding: var(--fixed-button-padding-y) var(--fixed-button-padding-x);
  font-size: var(--fixed-button-font-size);
  background-color: var(--light-accent);
  color: var(--button-text-color);
  transition: transform var(--transition-speed) var(--transition-ease), box-shadow var(--transition-speed) var(--transition-ease), background-color var(--transition-speed) var(--transition-ease);
  border-radius: 50px;
  font-weight: 600;
  position: fixed;
  box-shadow: 0 5px 15px #00b89466;
}

.get-in-touch-fixed:hover {
  transform: translateY(var(--transform-translate-y-md)) scale(1.03);
  background-color: #0ca;
  box-shadow: 0 8px 20px #00b89499;
}

#message-box {
  background-color: var(--light-card-bg);
  color: var(--light-text);
  padding: var(--message-box-padding);
  border-radius: var(--message-box-border-radius);
  box-shadow: var(--message-box-shadow);
  z-index: 1001;
  text-align: center;
  animation: fade-in var(--transition-speed) ease-out;
  min-width: 300px;
  display: none;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

#message-box.show {
  display: block;
}

.banner {
  height: var(--banner-height);
  color: var(--button-text-color);
  text-shadow: var(--banner-text-shadow);
  background-image: url("family-cycle-bike-wide-1600x900.61621af5.jpg");
  background-position: center;
  background-size: cover;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
  overflow: hidden;
}

.banner:before {
  content: "";
  background-color: var(--banner-overlay-color);
  z-index: 1;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.banner-content {
  z-index: 2;
  position: relative;
}

.banner h1 {
  font-size: var(--banner-h1-font-size);
  margin-bottom: var(--margin-bottom-default);
  color: var(--button-text-color);
}

.social-icons {
  bottom: var(--social-icons-bottom);
  gap: var(--gap-large);
  z-index: 2;
  display: flex;
  position: absolute;
}

.social-icons a {
  color: var(--button-text-color);
  font-size: var(--social-icons-font-size);
  transition: transform var(--transition-speed) var(--transition-ease), color var(--transition-speed) var(--transition-ease);
}

.social-icons a:hover {
  transform: translateY(var(--transform-translate-y-md)) scale(1.1);
  color: var(--light-accent);
}

.about-content {
  max-width: var(--max-width-md);
  text-align: left;
  background-color: var(--light-card-bg);
  border-radius: var(--border-radius-medium);
  box-shadow: var(--box-shadow-large);
  padding: var(--about-padding);
  border: 1px solid var(--light-border);
  transition: background-color var(--transition-speed) var(--transition-ease), box-shadow var(--transition-speed) var(--transition-ease);
  margin: 0 auto;
}

.about-content p {
  margin-bottom: var(--margin-bottom-default);
  font-size: var(--about-p-font-size);
}

.terms-content, .privacy-content {
  max-width: var(--max-width-sm);
  padding: var(--terms-privacy-padding);
  margin: 0 auto;
}

.terms-content h1, .privacy-content h1 {
  margin-bottom: var(--terms-privacy-h1-margin-bottom);
  text-align: center;
}

.terms-content h2, .privacy-content h2 {
  margin-top: var(--terms-privacy-h2-margin-top);
  margin-bottom: var(--terms-privacy-h2-margin-bottom);
  color: var(--light-primary);
}

.terms-content p, .privacy-content p {
  margin-bottom: var(--margin-bottom-default);
  line-height: var(--line-height-default);
}

.privacy-content ul, .terms-content ul {
  padding-left: var(--padding-default);
  margin-bottom: var(--margin-bottom-default);
  list-style: none;
}

.privacy-content ul li, .terms-content ul li {
  padding-left: var(--list-item-padding-left);
  margin-bottom: var(--list-item-margin-bottom);
  line-height: var(--list-item-line-height);
  position: relative;
}

.input-group.valid {
  border-color: var(--valid-color);
  box-shadow: 0 0 5px var(--valid-shadow);
}

.input-group.invalid {
  border-color: var(--invalid-color);
  box-shadow: 0 0 5px var(--invalid-shadow);
}

.input-group.valid i {
  color: var(--valid-color);
}

.input-group.invalid i {
  color: var(--invalid-color);
}

@keyframes fade-in {
  from {
    opacity: 0;
    transform: translate(-50%, -60%);
  }

  to {
    opacity: 1;
    transform: translate(-50%, -50%);
  }
}

.coming-soon-content {
  text-align: center;
  padding: 50px 20px;
}

.coming-soon-icon {
  color: var(--light-primary);
  margin-bottom: 30px;
  font-size: 64px;
}

.newsletter-signup {
  background-color: var(--light-card-bg);
  border-radius: var(--border-radius-medium);
  max-width: 500px;
  box-shadow: var(--box-shadow-medium);
  margin: 40px auto;
  padding: 30px;
}

.newsletter-signup .input-group {
  gap: 10px;
  display: flex;
}

.newsletter-signup input {
  border: 1px solid var(--light-input-border);
  border-radius: var(--border-radius-small);
  background-color: var(--light-input-bg);
  color: var(--light-text);
  flex: 1;
  padding: 12px;
}

.sitemap-content {
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 30px;
  padding: 40px 0;
  display: grid;
}

.sitemap-section {
  background-color: var(--light-card-bg);
  border-radius: var(--border-radius-medium);
  box-shadow: var(--box-shadow-medium);
  padding: 25px;
}

.sitemap-section h2 {
  color: var(--light-primary);
  align-items: center;
  gap: 10px;
  margin-bottom: 20px;
  font-size: 1.3em;
  display: flex;
}

.sitemap-section h2 i {
  color: var(--light-accent);
}

.sitemap-section ul {
  padding: 0;
  list-style: none;
}

.sitemap-section ul li {
  margin-bottom: 12px;
}

.sitemap-section ul li a {
  color: var(--light-text);
  transition: color var(--transition-speed) var(--transition-ease);
  padding: 5px 0;
  text-decoration: none;
  display: inline-block;
}

.sitemap-section ul li a:hover {
  color: var(--light-primary);
}

@media (width <= 480px) {
  .banner h1 {
    font-size: 2em;
  }

  .social-icons {
    font-size: 22px;
  }

  .card h3 {
    font-size: 1.4em;
  }

  .card p {
    font-size: .9em;
  }

  .get-in-touch-fixed {
    padding: 10px 18px;
    font-size: .9em;
    bottom: 15px;
    right: 15px;
  }

  .contact-methods, .contact-form {
    padding: 20px;
  }
}

@media (width <= 600px) {
  .logo {
    justify-content: center;
    width: 100%;
    margin-bottom: 10px;
  }

  .nav-mobile {
    height: calc(100vh - 130px);
    top: 130px;
  }

  .banner {
    height: 450px;
  }

  .banner h1 {
    font-size: 2em;
  }

  .social-icons {
    font-size: 24px;
  }

  section {
    padding: 50px 0;
  }

  .card {
    padding: var(--padding-default);
  }

  .card h3 {
    font-size: 1.6em;
  }

  .card p {
    font-size: .95em;
  }

  .contact-methods, .contact-form {
    padding: var(--gap-large);
  }

  .about-content {
    padding: 30px;
  }

  .footer-links {
    flex-direction: column;
    gap: 10px;
  }
}

@media (width <= 768px) {
  .banner h1 {
    font-size: 2.5em;
  }

  .newsletter-signup .input-group {
    flex-direction: column;
  }

  .sitemap-section {
    padding: 20px;
  }
}

@media (width <= 900px) {
  .banner h1 {
    font-size: 3em;
  }

  .social-icons {
    bottom: var(--margin-bottom-default);
    gap: var(--gap-medium);
  }

  .social-icons a {
    font-size: 26px;
  }

  .card-grid {
    grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  }

  .contact-content {
    flex-direction: column;
    gap: 30px;
  }

  .contact-methods, .contact-form {
    min-width: unset;
    flex: 100%;
  }

  .get-in-touch-fixed {
    bottom: var(--margin-bottom-default);
    right: var(--margin-bottom-default);
